@import '~assets/styles/h3Imports';

.notifications-card {
  max-width: em(1014);
  padding: 0 em(16);

  @media #{$tablet-up} {
    padding: 0 em(24);
  }
}

.notification-wrap {
  border-bottom: 1px solid var(--gray-1);
  display: flex;
}

.notification-wrap:last-child {
  border-bottom: none;
}

.image-wrap {
  display: flex;
  min-width: em(64);
  padding: em(24) 0;
}

.image {
  width: em(40);
}

.card-text-block {
  padding: em(24) em(30) em(24) 0;

  h3,
  h4,
  h5 {
    margin: 0;
  }
}

.card-text-title {
  font-size: em(19);
  font-weight: bold;
  padding-bottom: em(8);
}

.explanation-wrap {
  color: var(--gray-4);
  font-size: em(14);
  font-weight: 600;
}

.togle-wrap {
  display: flex;
  margin-left: auto;
  padding: em(24) 0;
  width: em(42);
}

.togle-wrap input {
  margin: 0;
}

@media #{$small-down} {
  .image-wrap {
    display: block;
  }

  .explanation-wrap {
    display: grid;
    grid-gap: em(8);
  }

  .togle-wrap {
    display: block;
  }
}
